.loader {
  text-indent: -9999em;
  width: 80px;
  height: 80px;
  background: -o-linear-gradient(left, #212121 10%, rgba(255, 255, 255, 0) 42%);
  background: -ms-linear-gradient(left, #212121 10%, rgba(255, 255, 255, 0) 42%);
  background: linear-gradient(to right, #212121 10%, rgba(255, 255, 255, 0) 42%);
  border-radius: 50%;
  margin-top: 20px;
  margin-left: 60px;
  font-size: 10px;
  animation: load3 1.4s linear infinite;
  position: relative;
  transform: translateZ(0);
}

.loader:before {
  width: 50%;
  height: 50%;
  content: "";
  background: #212121;
  border-radius: 100% 0 0;
  position: absolute;
  top: 0;
  left: 0;
}

.loader:after {
  width: 75%;
  height: 75%;
  content: "";
  background: #fafafa;
  border-radius: 50%;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes load3 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loader.is-hidden {
  display: none;
}

